import React from "react";
import { Layout, Row, Col, Input } from "antd";
import { LoggedinHeader } from "../../containers";
import "../manageOrg/manageOrg.scss";
import AccSidebar from "./AccSidebar";
import Grid from "../table/Table";
import Buttons from "../button/Button";
import { Link } from "react-router-dom";
import { AudioOutlined } from "@ant-design/icons";
import Pencil from "../../assets/icons/pencil.svg";

const { Search } = Input;

const suffix = (
  <AudioOutlined
    style={{
      fontSize: 16,
      color: "#1890ff",
    }}
  />
);

const { Content } = Layout;
class ShowAllOrganizations extends React.Component {
  constructor(props) {
    super(props);

    const columns = [
      {
        title: "Organization Name ",
        dataIndex: "name",
        key: "name",
        width: "22%",
        render: (name) => {
          return {
            children: name,
            props: {
              "data-title": "Organization Name",
            },
          };
        },
      },
      {
        title: "Organization Id",
        dataIndex: "identifier",
        key: "identifier",
        width: "20%",
        render: (identifier) => {
          return {
            children: identifier,
            props: {
              "data-title": "Organization Id",
            },
          };
        },
      },
      {
        title: "Start Date ",
        dataIndex: "start_date",
        key: "start_date",
        width: "20%",
        render: (start_date) => {
          return {
            children: start_date,
            props: {
              "data-title": "Start date",
            },
          };
        },
      },
      {
        title: "End Date",
        dataIndex: "end_date",
        key: "end_date",
        width: "20%",
        render: (start_date) => {
          return {
            children: start_date,
            props: {
              "data-title": "End date",
            },
          };
        },
      },
      {
        title: "Status",
        dataIndex: "status_id",
        key: "status_id",
        width: "10%",

        render: (status_id) => {
          return {
            children: (
              <div className="primary-tag-rounded tag">
                <span className="circle mr-1"></span>
                {status_id === 1 ? "Active" : "InActive"}
              </div>
            ),
            props: {
              "data-title": "Status",
            },
          };
        },
        // render: () => <div className="secondary-tag-rounded tag">Inactive</div>,
      },
      {
        title: "Actions",
        dataIndex: "",
        key: "",
        width: "10%",
        render: (record) => {
          const path = `/amcc/editorg/${record.organization_id}`;
          return {
            children: (
              <Link to={path}>
                <img src={Pencil} alt="Actions" />
              </Link>
            ),
            props: {
              "data-title": "Actions",
            },
          };
        },
      },
    ];
    this.state = {
      columns: columns,
      data: [],
    };
  }

  componentDidMount() {
    let { organizations } = this.props;
    organizations.forEach((org) => {
      org.start_date = org.start_date.replaceAll("-", " / ");
      org.end_date = org.end_date.replaceAll("-", " / ");
    });
    this.setState({ data: organizations }, () => {
      this.onSearch("");
    });
  }

  filterIt(arr, searchKey) {
    if (!searchKey || searchKey === "" || searchKey === null) {
      return arr;
    }
    return arr.filter(function (obj) {
      return Object.keys(obj).some(function (key) {
        if (typeof obj[key] === "string") {
          return obj[key].includes(searchKey);
        }
      });
    });
  }

  onSearch = (value) => {
    const { data } = this.state;
    const filterDt = this.filterIt(data, value);
    this.setState({ filterData: filterDt });
  };

  render() {
    return (
      <>
        {" "}
        <Row className="inner-header">
          <LoggedinHeader amCCUser />
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
              <AccSidebar />
              <Content className="">
                <Row className="py-3">
                  <h1>Organizations </h1>
                </Row>
                <Col span={24}>
                  <Row className="pt-1 pb-3 px-3 px-lg-0 sm-direction-reverse">
                    <Col span={24} lg={18} className="search-row mt-2 mt-lg-0">
                      <Row>
                        <Col span={24} lg={9} className="search-box">
                          <Search
                            disabled={true}
                            placeholder="Search"
                            onSearch={this.onSearch}
                          />
                        </Col>
                      </Row>
                    </Col>

                    <Col
                      span={24}
                      lg={6}
                      className="d-flex align-items-center justify-content-end"
                    >
                      <Link to="/amcc/createorg">
                        <Buttons value="Create Organization" />
                      </Link>
                    </Col>
                  </Row>
                </Col>
                <Grid
                  key="manageOrg"
                  rowKey="identifier"
                  columns={this.state.columns}
                  data={this.state.filterData}
                />
              </Content>
            </Layout>
          </Layout>
        </Row>
      </>
    );
  }
}

export default ShowAllOrganizations;
