import React, { useState } from "react";
import { Form, Select, Input, Button, DatePicker, Alert, Upload } from "antd";
import AddIcoModal from "../../components/modal/AddIcoModal";
import ReactHtmlParser from "react-html-parser";
import moment from "moment";
import authHeader from "../../services/auth-header";
import axios from "axios";
import {CheckCircleTwoTone, UploadOutlined } from '@ant-design/icons';
import FloatLabel from "../form/FloatLabels";



import "../form/form.scss";
import { BASE_URL } from "../../userconfig";
const { TextArea } = Input;
const { Option } = Select;

const CreateOpenHotlineStepOne = ({
  next,
  stepOneOpenLineInfo,
  handleStepOneOnChange,
  handleStepOneOnSelect,
  handleStepOneUpload,
  handleStepOnePreview,
  handleBeforeUpload,
  handleStepOneDatePickerChange,
  addIcon,
  getIconText,
}) => {
  const [hotlineForm, setHotlineForm] = useState({
    iconText: "",
  });

  const [error, showError] = useState(false);
  const [disabledbtn, setdisabledbtn] = useState(true);
  const [form] = Form.useForm();
  const [hotlineErrorUpdated, sethotlineErrorUpdated] = useState("");

  const stepOneNext = (e) => {
    e.preventDefault();
    console.log("file", stepOneOpenLineInfo.file)
    if (
        stepOneOpenLineInfo.openelineName &&
        stepOneOpenLineInfo.description &&
        stepOneOpenLineInfo.file &&
        stepOneOpenLineInfo.status &&
        stepOneOpenLineInfo.sDate
    ) {
      showError(false);
      setdisabledbtn(false);
      next();
    } else {
      showError(true);
    }
  };

  const disabledDate = (current) => {
    return current && current < moment().startOf("day");
  };

  const CheckHotlineName = (e) => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };

    axios
      .post(
        BASE_URL+"checkhotline/",
        { hotline_name: stepOneOpenLineInfo.openelineName},
        options
      )
      .then((res) => {
        if (res.data) {
          sethotlineErrorUpdated(res.data[0]);
        }
      });
  };

  const { RangePicker } = DatePicker;

  return (
    <Form data-test='createHotlineStepOne' className="input-md w-md-100 w-75 floating-label" name="validate_other" form={form}>
      {error && (
        <Form.Item>
          <Alert
            message="Please fill the fields marked with asterisk(*)"
            type="error"
            showIcon
          ></Alert>
        </Form.Item>
      )}
      
      <FloatLabel label="Openeline Name">
      <Form.Item
        className="required"
        name="openelineName"
        rules={[
          {
            required: true,
            message: "Please enter directory name",
          },
        ]}
      >
        <TextArea
          showCount
          maxLength={80}
          value={stepOneOpenLineInfo.openelineName}
          onChange={(e) => handleStepOneOnChange(e)}
          onBlur={(e) => CheckHotlineName(e)}
          id={"openelineName"}
          className="input-control textarea-input"
          htmlFor="openelineName"
        />
        {hotlineErrorUpdated == "status: false" && (
          <CheckCircleTwoTone className="successCheck" twoToneColor="#52c41a" />
        )}
        {hotlineErrorUpdated == "status: true" && (
          <Alert
            message="Sorry! This hotline already exists in the system. Please use a different name."
            type="error"
            showIcon
          ></Alert>
        )}
      </Form.Item>
      </FloatLabel>
      <FloatLabel label="Description">
      <Form.Item
        className="required"
        name="description"
        rules={[
          {
            required: true,
            message: "Please enter description",
          },
        ]}
      >
        <>
          <TextArea
            className="input-control textarea-input"
            value={stepOneOpenLineInfo.description}
            onChange={(e) => handleStepOneOnChange(e)}
            id={"description"}
            htmlFor="description"
          />
        </>
      </Form.Item>

      </FloatLabel>

      <FloatLabel label="Date">

      <Form.Item className="input-control datepicker-wrapper">
        <RangePicker
          className="datepicker w-100 w-lg-50 mr-0 mr-lg-5 required"
          name="sDate"
          value={stepOneOpenLineInfo.sDate}
          onChange={(date) => handleStepOneDatePickerChange(date, "sDate")}
          disabledDate={disabledDate}
          rules={[
            {
              required: true,
              message: "Please select start date",
            },
          ]}
        />
      </Form.Item>
    
      </FloatLabel>
      <FloatLabel label="Status">
      <Form.Item
        className="required"
        name="status"
        rules={[
          {
            required: true,
            message: "Please select status",
          },
        ]}
      >
        <>
          <Select
            placeholder="Status"
            className="input-control w-100 w-lg-50"
            value={stepOneOpenLineInfo.status}
            htmlFor="status"
            onSelect={(value) => handleStepOneOnSelect(value, "status")}
          >
            <Option value="" disabled>
              
            </Option>
            <Option value="5">Active</Option>
            <Option value="4">Inactive</Option>
          </Select>
        </>
      </Form.Item>
      </FloatLabel>
      
      <Form.Item className="">
        <Form.Item
          className="upload-control"
          name="upload"
          valuePropName="fileList"
        >
            <Upload
             action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
             listType="picture"
             fileList={stepOneOpenLineInfo.file}
             onChange={(value) => handleStepOneUpload(value, "file")}
            //  beforeUpload={(value)=>handleBeforeUpload(value)}
          >
                <Button icon={<UploadOutlined />}>Upload</Button>
            </Upload>
          {/* <AddIcoModal data-test="addIconComponent" addIcon={addIcon} /> */}
          {getIconText(stepOneOpenLineInfo.hotlineIconId) && (
            <div className="ant-upload-list ant-upload-list-picture">
              <div className="ant-upload-list-item ant-upload-list-item-list-type-picture">
                <div className="ant-upload-list-item-info d-inline-flex w-100">
                  <span className="d-inline-flex align-items-center">
                    <a className="ant-upload-list-item-thumbnail">
                      {ReactHtmlParser(stepOneOpenLineInfo.iconHtml)}
                    </a>

                    <span
                      className="ant-upload-list-item-name ant-upload-list-item-name-icon-count-1"
                      title={getIconText(stepOneOpenLineInfo.hotlineIconId)}
                    >
                      {getIconText(stepOneOpenLineInfo.hotlineIconId)}
                    </span>
                  </span>
                </div>
              </div>
            </div>
          )}
        </Form.Item>
      </Form.Item>
      <Form.Item>
        <Button type="primary" htmlType="submit" onClick={stepOneNext} data-test="stepOneNext">
          Next
        </Button>
      </Form.Item>
    </Form>
  );
};
export default CreateOpenHotlineStepOne;
