import React from "react";
import {
  Layout,
  Row,
  Col,
  Input,
  Avatar,
  Form,
  DatePicker,
  Divider,
  Button,
} from "antd";
import { LoggedinHeader } from "../../containers";
import "../manageOrg/manageOrg.scss";
import AccSidebar from "../manageOrg/AccSidebar";
import Pencil from "../../assets/icons/pencil.svg";
import FloatLabel from "./FloatLabels";

const { Content } = Layout;

const EditOrg = () => {
  const onFinish = (values) => {};
  const { RangePicker } = DatePicker;
  return (
    <>
      <Row className="inner-header">
        <LoggedinHeader amCCUser />
      </Row>
      <Row className="content-center pt-0">
        <Layout>
          <Layout>
            <AccSidebar />
            <Content>
            <Row className="py-3">
                <h1 className="mb-0">Organization Details</h1>
              </Row>
              <div className="mid-content mt-4">
                <Row>
                  <Col span={24}>
                    <Col
                      span={24}
                      className="d-flex justify-content-between align-items-center"
                    >
                      <h6 className="py-3 mb-0">Organization Details </h6>
                      <div>
                        <Button
                          type="link"
                          className="link pl-0 btn-link-primary ml-3"
                        >
                          <span className="icon icon-pencil" />
                          <span className="pl-2 d-none d-block-md">Edit Details</span>
                        </Button>
                      </div>
                    </Col>
                    <div className="avatar-wrapper pt-2 pb-4">
                      <Avatar>W</Avatar>
                      <span className="md-name">Woices</span>
                    </div>
                    <Form
                      className="input-md floating-label w-100"
                      name="validate_other"
                      onFinish={onFinish}
                    >
                      <Col span={24}>
                        <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                          <Col span={24} md={8} className="gutter-row">
                            <FloatLabel label="Organization Name">
                              <Form.Item
                                className="required"
                                name="organization name"
                                rules={[
                                  {
                                    required: true,
                                    message: "Please enter organization name ",
                                  },
                                ]}
                              >
                                <Input
                                  className="input-control"
                                  htmlFor="organization name"
                                />
                              </Form.Item>
                            </FloatLabel>
                          </Col>
                          <Col span={24} md={8} className="gutter-row">
                            <FloatLabel label="Organization Id">
                              <Form.Item
                                className="required"
                                name="organization id"
                                validateStatus="success"
                                rules={[
                                  {
                                    required: true,
                                    message: "Please enter organization id ",
                                  },
                                ]}
                              >
                                <Input
                                  className="input-control"
                                  htmlFor="organization name"
                                />
                              </Form.Item>
                            </FloatLabel>
                          </Col>
                          <Col span={24} md={8} className="gutter-row">
                            <FloatLabel label="Status">
                              <Form.Item
                                className="required"
                                name="status"
                                rules={[
                                  {
                                    required: true,
                                    message: "Please enter status ",
                                  },
                                ]}
                              >
                                <Input
                                  className="input-control"
                                  htmlFor="status"
                                />
                              </Form.Item>
                            </FloatLabel>
                          </Col>
                          <Col span={24} md={8} className="gutter-row">
                            <FloatLabel label="Select Date">
                              <Form.Item className="input-control datepicker-wrapper required">
                                <RangePicker className="w-100 " />
                              </Form.Item>
                            </FloatLabel>
                          </Col>
                        </Row>
                      </Col>
                    </Form>
                  </Col>
                </Row>
              </div>

              <div className="mid-content mt-4">
                <Row>
                  <Col
                    span={24}
                    className="d-flex justify-content-between align-items-center"
                  >
                    <Row className="w-100">
                      <Col span={24} md={12}>
                        <h6 className="py-3 mb-0">Seeded Users Details </h6>
                      </Col>
                      <Col span={24} md={12} className="d-flex justify-content-left justify-content-md-end align-items-center my-3 my-md-0">
                          <Button className="mr-4" type="secondary">
                            Add Seeded User
                          </Button>
                          <Button
                            type="link"
                            className="link pl-0 btn-link-primary ml-3"
                          >
                            <span className="icon icon-pencil" />
                            <span className="pl-2 d-none d-block-md">Edit Details</span>
                          </Button>
                      </Col>
                    </Row>
                  </Col>
                  <div className="avatar-wrapper pt-2 pb-4">
                    <Avatar>G</Avatar>
                    <span className="md-name">Gunjan Siddha</span>
                  </div>
                  <Col span={24}>
                    <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                      <Col span={24} md={8} className="gutter-row">
                        <FloatLabel label="User Name">
                          <Form.Item
                            name="uname"
                            rules={[
                              {
                                required: true,
                                message: "Please enter user name ",
                              },
                            ]}
                            className="required"
                          >
                            <Input className="input-control" htmlFor="uname" />
                          </Form.Item>
                        </FloatLabel>
                      </Col>
                      <Col span={24} md={8} className="gutter-row">
                        <FloatLabel label="User Id">
                          <Form.Item
                            name="uid"
                            rules={[
                              {
                                required: true,
                                message: "Please enter user id ",
                              },
                            ]}
                            className="required"
                          >
                            <Input className="input-control" htmlFor="uid" />
                          </Form.Item>
                        </FloatLabel>
                      </Col>
                      <Col span={24} md={8} className="gutter-row">
                        <FloatLabel label="Temporary Password">
                          <Form.Item
                            name="temppassword"
                            className="pwd"
                            rules={[
                              {
                                required: true,
                                message:
                                  "Please enter your temporary password!",
                              },
                            ]}
                          >
                            <Input.Password
                              className="input-control"
                              htmlFor="temppassword"
                              className="required"
                            />
                          </Form.Item>
                        </FloatLabel>
                      </Col>
                      <Col span={24} md={8} className="gutter-row">
                        <FloatLabel label="Email Id">
                          <Form.Item
                            name="emailid"
                            rules={[
                              {
                                required: true,
                                message: "Please enter email id",
                              },
                            ]}
                            className="required"
                          >
                            <Input
                              className="input-control"
                              htmlFor="emailid"
                            />
                          </Form.Item>
                        </FloatLabel>
                      </Col>
                      <Col span={24} md={8} className="gutter-row">
                        <FloatLabel label="Select Date">
                          <Form.Item
                            className="input-control datepicker-wrapper"
                            name="date"
                          >
                            <RangePicker
                              className="new w-100"
                              rules={[
                                {
                                  required: true,
                                  message: "Please enter date",
                                },
                              ]}
                              className="required"
                            />
                          </Form.Item>
                        </FloatLabel>
                      </Col>
                      <Col span={24} md={8} className="gutter-row">
                        <FloatLabel label="Status">
                          <Form.Item
                            name="status"
                            rules={[
                              {
                                required: true,
                                message: "Please enter status",
                              },
                            ]}
                            className="required"
                          >
                            <Input className="input-control" htmlFor="status" />
                          </Form.Item>
                        </FloatLabel>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </div>
            </Content>
          </Layout>
        </Layout>
      </Row>
    </>
  );
};

export default EditOrg;
