import React, { useEffect } from "react";
import ShowAllOrganizations from "./ShowAllOrganizations";
import CreateOrg from "./CreateOrg";
import { connect } from "react-redux";
import { getManageOrgAsync } from "../../actions/manageOrg";

const OrganizationContainer = (props) => {
  useEffect(() => {
    const { dispatch } = props;
    dispatch(getManageOrgAsync());
  }, []);
  return props.manageOrgData?.length > 0 ? (
    <ShowAllOrganizations organizations={props.manageOrgData} />
  ) : (
    <CreateOrg />
  );
};

function mapStateToProps(state) {
  const { manageOrgData } = state.manageOrg;
  return {
    manageOrgData,
  };
}

export default connect(mapStateToProps)(OrganizationContainer);
