import React, { useEffect, useState } from "react";
import { Form, Select, Input, Button, DatePicker, Alert, Row, Layout } from "antd";
import AddIcoModal from "../../components/modal/AddIcoModal";
import ReactHtmlParser from "react-html-parser";
import * as AppStorage from "../../utils/AppStorage";

import moment from "moment";
import authHeader from "../../services/auth-header";
import axios from "axios";
import { CheckCircleTwoTone } from '@ant-design/icons';
import { LoggedinHeader } from "../../containers";
import OrgSidebar from "../../containers/OrgSidebar";
import { useHistory, useLocation } from 'react-router-dom';
import "../form/form.scss";
import { BASE_URL } from "../../userconfig";

const { TextArea } = Input;
const { Option } = Select;
const { Content } = Layout;
const EditHotLine = ({
    props
}) => {
    const [hotLineData, sethotLineData] = useState({
        end_date: "",help_text: "",
        hotline_icon_id: "", hotline_id: "",
        name: "",start_date: "",status_id: ""
    });

    const [iconHtml, seticonHtml] = useState('');
    const [error, showError] = useState(false);
    const history = useHistory();

    const [disabledbtn, setdisabledbtn] = useState(true);
    const [form] = Form.useForm();
    const [hotlineErrorUpdated, sethotlineErrorUpdated] = useState("");
    let location = useLocation();


    const addIcon = (values, id) => {
        sethotLineData({
          ...hotLineData,
          hotline_icon_id: id,
        });
        seticonHtml(values);
    };

    const getIconText = (id) => {
        switch (id) {
            case "1":
                return "Sexual Harassment";
            case "2":
                return "Violence";
            case "3":
                return "Racism";
            case "4":
                return "Cyber Bullying";
            case "5":
                return "Fight";
            case "6":
                return "Discrimination";
            case "7":
                return "Slap";
            default:
                return "";
        }
   };

   const setIcon=(id)=>{
    switch (id) {
        case "1":
            return '<span className="icon icon-sexual-harassment-1 ant-tooltip-open" />';
        case "2":
            return <span className="icon icon-violence ant-tooltip-open" />;
        case "3":
            return <span className="icon icon-violence ant-tooltip-open" />;
        case "4":
            return <span className="icon icon-violence ant-tooltip-open" />;
        case "5":
            return <span className="icon icon-violence ant-tooltip-open" />;
        case "6":
            return <span className="icon icon-violence ant-tooltip-open" />;
        case "7":
            return <span className="icon icon-violence" />;
        default:
            return "";
    }
   }


    useEffect(() => {
        CheckHotlineName();
        const sDate = moment(location.state.end_date).format('YYYY-MM-DD');
        const eDate = moment(location.state.start_date).format('YYYY-MM-DD');
        let dates = [moment(sDate, "YYYY/MM/DD"), moment(eDate, "YYYY/MM/DD")]
        sethotLineData({
            end_date: "",
            help_text: location.state.help_text,
            hotline_icon_id: location.state.hotline_icon_id,
            hotline_id: location.state.hotline_id,
            name: location.state.name,
            start_date: dates,
            status_id: location.state.status_id.toString()
        })
        const test=setIcon(location.state.hotline_icon_id.toString());
        seticonHtml(test);
    }, [])


    const handleStepOneOnChange = (e) => {
        sethotLineData({ ...hotLineData, [e.target.id]: e.target.value });
    };

    const handleStepOneDatePickerChange = (date, name) => {
        sethotLineData({ ...hotLineData, [name]: date });
    };

    const editHotline = (e) => {
        e.preventDefault();
        const user = JSON.parse(AppStorage.get("user"));
        const data = {
            end_date: moment(hotLineData.start_date[0]).format('DD-MM-YYYY'),
            help_text: hotLineData.help_text,
            hotline_icon_id: hotLineData.hotline_icon_id,
            hotline_id: hotLineData.hotline_id,
            name: hotLineData.name,
            start_date: moment(hotLineData.start_date[0]).format('DD-MM-YYYY'),
            status_id: parseInt(hotLineData.status_id),
            updated_by: user.user_details.org_user_id
        };
        saveHotLine(data);
    };

    const saveHotLine = (data) => {
        const token = authHeader();
        let hotLineId = hotLineData.hotline_id;
        delete hotLineData.hotline_id;
        const options = {
            headers: {
                Authorization: token,
                Accept: "application/json, text/plain",
                "Content-Type": "application/json;charset=UTF-8",
            },
        };
        axios
            .put(BASE_URL + "hotline/" + hotLineId, data, options)
            .then((res) => {
                if (res.status === 200) {
                    history.push("/orgcc/hotlinedirectory");

                }
            });
    }

    const disabledDate = (current) => {
        return current && current < moment().startOf("day");
    };

    const CheckHotlineName = (e) => {
        const token = authHeader();
        const options = {
            headers: {
                Authorization: token,
                Accept: "application/json, text/plain",
                "Content-Type": "application/json;charset=UTF-8",
            },
        };

        axios.post(   BASE_URL + "checkhotline/",{ hotline_name: "test3" },options)
            .then((res) => {
                if (res.data) {
                    sethotlineErrorUpdated(res.data[0]);
                }
            });
    };

    const { RangePicker } = DatePicker;

    return (
        <>
            <Row className="inner-header">
                <LoggedinHeader amORGUser />
            </Row>
            <Row className="content-center pt-0" data-test="manageEmp">
                <Layout>
                    <Layout>
                        <OrgSidebar amORGUser />
                        <Content className="">
                            <Row className="py-3">
                                <h1>Edit Hotline</h1>
                            </Row>
                            <div className="mid-content mt-4">
                             <Form data-test='createHotlineStepOne' className="input-md w-md-100 w-75" name="validate_other" form={form}>
                                    {error && (
                                        <Form.Item>
                                            <Alert
                                                message="Please fill the fields marked with asterisk(*)"
                                                type="error"
                                                showIcon
                                            ></Alert>
                                        </Form.Item>
                                    )}

                                    <Form.Item
                                        className="required"
                                        name="directoryName"
                                        rules={[
                                            {
                                                required: true,
                                                message: "Please enter directory name",
                                            },
                                        ]}
                                    >
                                        <TextArea
                                            showCount
                                            maxLength={80}
                                            value={hotLineData.name}
                                            onChange={(e) => handleStepOneOnChange(e)}
                                            id={"name"}
                                            className="input-control textarea-input"
                                            placeholder="Directory Name"
                                            htmlFor="directoryName"
                                        />
                                        {hotlineErrorUpdated == "status: false" && (
                                            <CheckCircleTwoTone className="successCheck" twoToneColor="#52c41a" />
                                        )}
                                    </Form.Item>
                                    <Form.Item
                                        className="required"
                                        name="description"
                                        rules={[
                                            {
                                                required: true,
                                                message: "Please enter description",
                                            },
                                        ]}
                                    >
                                        <>
                                            <TextArea
                                                className="input-control textarea-input"
                                                value={hotLineData.help_text}
                                                onChange={(e) => handleStepOneOnChange(e)}
                                                id={"help_text"}
                                                placeholder="Description"
                                                htmlFor="description"
                                            />
                                        </>
                                    </Form.Item>

                                    <Form.Item className="input-control datepicker-wrapper">
                                        <RangePicker
                                            className="datepicker w-100 w-lg-50 mr-0 mr-lg-5 required"
                                            name="start_date"
                                            value={hotLineData.start_date}
                                            onChange={(date) => handleStepOneDatePickerChange(date, "start_date")}
                                            rules={[
                                                {
                                                    required: true,
                                                    message: "Please select start date",
                                                },
                                            ]}
                                        />
                                    </Form.Item>
                                    <Form.Item
                                        className="required"
                                        name="status"
                                        rules={[
                                            {
                                                required: true,
                                                message: "Please select status",
                                            },
                                        ]}
                                    >
                                        <>
                                            <Select
                                                placeholder="Status"
                                                className="input-control w-100 w-lg-50"
                                                value={hotLineData.status_id}
                                                htmlFor="status"
                                                onSelect={(value) => handleStepOneOnSelect(value, "status")}
                                            >
                                                <Select.Option value="" disabled>
                                                    Status
                                                </Select.Option>
                                                <Select.Option value="5">Active</Select.Option>
                                                <Select.Option value="4">Inactive</Select.Option>
                                            </Select>
                                        </>
                                    </Form.Item>
                                    <Form.Item className="">
                                        <Form.Item
                                            className="upload-control"
                                            name="upload"
                                            valuePropName="fileList"
                                        >
                                            <AddIcoModal data-test="addIconComponent" addIcon={addIcon} />
                                            {getIconText(hotLineData.hotline_icon_id.toString()) && (
                                                <div className="ant-upload-list ant-upload-list-picture">
                                                    <div className="ant-upload-list-item ant-upload-list-item-list-type-picture">
                                                        <div className="ant-upload-list-item-info d-inline-flex w-100">
                                                            <span className="d-inline-flex align-items-center">
                                                                <a className="ant-upload-list-item-thumbnail">
                                                                    {ReactHtmlParser(iconHtml)}
                                                                </a>

                                                                <span
                                                                    className="ant-upload-list-item-name ant-upload-list-item-name-icon-count-1"
                                                                    title={getIconText(hotLineData.hotline_icon_id.toString())}
                                                                >
                                                                    {getIconText(hotLineData.hotline_icon_id.toString())}
                                                                </span>
                                                            </span>
                                                        </div>
                                                    </div>
                                                </div>
                                            )}
                                        </Form.Item>
                                    </Form.Item>
                                    <Form.Item>
                                        <Button type="primary" htmlType="submit" onClick={editHotline} data-test="stepOneNext">
                                            Submit
                                    </Button>
                                    </Form.Item>
                                </Form>
                            </div>
                        </Content>
                    </Layout>
                </Layout>
            </Row>
        </>
    );
};
export default EditHotLine;
