import React from "react";
import { Popover } from "antd";
import DeleteHotlineModal from "../../components/modal/DeleteHotlineModal";
import dotsicon from "../../assets/icons/dots-icon.svg";
import { Link } from "react-router-dom";


const content = (test)=>(
  <div>
    <ul>
      <Link to={{pathname:'/orgcc/edithotline',state:test}}>
      <li>
      <a href="/orgcc/edithotline">Edit</a> </li>
        </Link>
      <li>
        <DeleteHotlineModal hotlineId={test.hotline_id} />
      </li>
    </ul>

  </div>
);

class HotlinePopover extends React.Component {
  state = {
    visible: false,
  };

  hide = () => {
    this.setState({
      visible: false,
    });
  };

  hide = () => {
    this.setState({
      visible: false,
    });
  };

  handleVisibleChange = (visible) => {
    this.setState({ visible });
  };

  render() {
    return (
      <>
        <Popover
          placement="bottom"
          content={content(this.props.data)}
          trigger="click"
          visible={this.state.visible}
          onVisibleChange={this.handleVisibleChange}
        >
          <span className="ico-grid-dots">
            <img src={dotsicon} alt="DotIcon" className="pr-4" />
          </span>
        </Popover>
      </>
    );
  }
}

export default HotlinePopover;
