import React, { useState, useEffect } from "react";
import {
  Layout,
  Row,
  Col,
  Input,
  Avatar,
  Form,
  DatePicker,
  Button,
  Select,
  Modal,
  Alert,
} from "antd";
import moment from "moment";
import DeleteModal from "../../components/modal/DeleteModal";
import { LoggedinHeader } from "../../containers";
import "../manageOrg/manageOrg.scss";
import AccSidebar from "../manageOrg/AccSidebar";
import FloatLabel from "./FloatLabels";
import ResetPwdModal from "../modal/ResetPwdModal";
import AssignedUsers from "../manageOrg/AssignedUsers";
import { BASE_URL } from "../../userconfig";
import axios from "axios";
import authHeader from "../../services/auth-header";

const { Content } = Layout;

const EditOrg = (props) => {
  const [form] = Form.useForm();
  const orgID = props.computedMatch?.params.id;
  const [error, showError] = useState(false);
  const [emailValidationError, setEmailValidationError] = useState(false);
  const [mobileValidationError, setMobileValidationError] = useState(false);
  const [organizationName, setOrganizationName] = useState("");
  const [seededUserName, setSeededUserName] = useState("");
  const [seededUserEmail, setSeededUserEmail] = useState("");
  const [organizationDetails, setOrganizationDetails] = useState({
    name: "",
    identifier: "",
    org_id: "",
    status: "",
    date1: "",
  });
  const [seededUserDetails, setSeededUserDetails] = useState({
    firstName: "",
    lastName: "",
    status2: "",
    email: "",
    date2: "",
    mobile: "",
  });
  const [seededUsers, setSeededUsers] = useState([]);
  const [editVisible, setEditVisible] = useState(false);
  const [deleteClick, setDeleteClick] = useState(false);

  useEffect(() => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    axios.get(BASE_URL + `getOrganization/${orgID}/`, options).then((res) => {
      const data = res?.data;
      setOrganizationDetails({
        name: data.organization.name,
        identifier: data.organization.identifier,
        status: data.organization.status_id.toString(),
        date1: [
          moment(data.organization.start_date, "YYYY-MM-DD"),
          moment(data.organization.end_date, "YYYY-MM-DD"),
        ],
      });
      setOrganizationName(data.organization.name);
      form.setFieldsValue({
        name: data.organization.name,
        identifier: data.organization.identifier,
      });
      let appendedUsers = [];
      data.seeded_user.map((value) => {
        if(!value.soft_delete) {
          appendedUsers = [
            ...appendedUsers,
            {
              firstName: value.first_name,
              lastName: value.last_name,
              status2: value.status_id.toString(),
              email: value.email,
              date2: [
                moment(value.start_date, "YYYY-MM-DD"),
                moment(value.end_date, "YYYY-MM-DD"),
              ],
              mobile: value.mobile_number,
            },
          ];
        }
      });
      setSeededUsers(appendedUsers);
    });
  }, []);

  const onDelete = () => {
    setDeleteClick(true);
  };

  const handleEditCancel = () => {
    setEditVisible(false);
  };

  const handleEditOk = () => {
    if (
      seededUserDetails.firstName &&
      seededUserDetails.lastName &&
      seededUserDetails.status2 &&
      seededUserDetails.mobile &&
      seededUserDetails.date2 &&
      seededUserDetails.email
    ) {
      showError(false);
      if (!emailValidationError && !mobileValidationError) {
        editUser();
      }
    } else {
      showError(true);
    }
  };

  const editUser = () => {
    seededUsers.forEach((user) => {
      if (
        JSON.stringify(user) !== JSON.stringify(seededUserDetails) &&
        user.email === seededUserDetails.email
      ) {
        let index = seededUsers.indexOf(user);
        let users = JSON.parse(JSON.stringify(seededUsers));
        users[index] = seededUserDetails;
        setSeededUsers(users);
      }
    });
    onSeededUserEditSubmit();
  };

  const handleOrganizationOnSelect = (value, name) => {
    setOrganizationDetails({ ...organizationDetails, [name]: value });
  };

  const handleOrganizationDatePickerChange = (date, name) => {
    setOrganizationDetails({ ...organizationDetails, [name]: date });
  };

  const handleSeededUserOnChange = (e) => {
    setSeededUserDetails({
      ...seededUserDetails,
      [e.target.id]: e.target.value,
    });
  };

  const handleSeededUserOnSelect = (value, name) => {
    setSeededUserDetails({ ...seededUserDetails, [name]: value });
  };

  const handleSeededUserDatePickerChange = (date, name) => {
    setSeededUserDetails({ ...seededUserDetails, [name]: date });
  };

  const validateEmailAddress = (e) => {
    var re = /\S+@\S+\.\S+/;
    if (!re.test(e.target.value)) {
      setEmailValidationError(true);
    } else {
      setEmailValidationError(false);
    }
  };

  const validateMobile = (e) => {
    var re = /^\d{10}$/;
    if (!re.test(e.target.value)) {
      setMobileValidationError(true);
    } else {
      setMobileValidationError(false);
    }
  };

  const handleDeleteOrganization = () => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };

    axios
      .delete(BASE_URL + `deleteOrganization/${orgID}/`, options)
      .then(() => {
        window.location = "/amcc/manageorganizations";
      });
  };

  const handleResetPasswordOk = () => {};

  const handleResetPasswordCancel = () => {};

  const onOrgEditSubmit = () => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    const data = {
      organization_id: orgID,
      start_date: organizationDetails.date1[0]
        ? moment(organizationDetails.date1[0]).format("DD-MM-YYYY")
        : "",
      end_date: organizationDetails.date1[1]
        ? moment(organizationDetails.date1[1]).format("DD-MM-YYYY")
        : "",
    };
    axios.put(BASE_URL + "saveOrganization/", data, options).then(() => {
      window.location = "/amcc/manageorganizations";
    });
  };

  const onSeededUserEditSubmit = () => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    const data = {
      first_name: seededUserDetails.firstName,
      last_name: seededUserDetails.lastName,
      email_from: seededUserEmail,
      email: seededUserDetails.email,
      status_id: parseInt(seededUserDetails.status2),
      start_date: seededUserDetails.date2[0]
        ? moment(seededUserDetails.date2[0]).format("DD-MM-YYYY")
        : "",
      end_date: seededUserDetails.date2[1]
        ? moment(seededUserDetails.date2[1]).format("DD-MM-YYYY")
        : "",
    };
    axios.put(BASE_URL + "seededUser/", data, options).then(() => {
      setEditVisible(false);
    });
  };

  const cancelEditing = () => {
    window.location = "/amcc/manageorganizations";
  };

  const disabledDate = (current) => {
    return current && current < moment().startOf("day");
  };

  const handleEditSeededUser = (email) => {
    setEditVisible(true);
    showError(false);
    setEmailValidationError(false);
    setMobileValidationError(false);
    let editableUser = {};
    seededUsers.forEach((seededUser) => {
      if (seededUser.email === email) {
        editableUser = seededUser;
      }
    });
    setSeededUserDetails(editableUser);
    setSeededUserName(editableUser.firstName);
    setSeededUserEmail(editableUser.email);
  };

  const handleDeleteSeededUser = (email) => {
    let filteredUsers = seededUsers?.filter((user) => user?.email !== email);
    setSeededUsers(filteredUsers);
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    axios.delete(BASE_URL + `seededUser/${email}`, options).then(() => {});
  };

  const { RangePicker } = DatePicker;

  return (
    <>
      <Row className="inner-header">
        <LoggedinHeader amCCUser />
      </Row>
      <Row className="content-center pt-0">
        <Layout>
          <Layout>
            <AccSidebar />
            <Content>
              <Row className="py-3">
                <h1 className="mb-0">Edit Organization</h1>
              </Row>
              <div className="mid-content">
                <Row>
                  <Col span={24}>
                    <Col
                      span={24}
                      className="d-flex justify-content-between align-items-center"
                    >
                      <h6 className="py-3 mb-0">Edit Organization</h6>
                      <div>
                        <Button
                          type="link"
                          className="link pl-0 btn-link-primary ml-3"
                          onClick={onDelete}
                        >
                          <span className="icon icon-delete" />
                          <span className="d-block-md">Delete</span>
                        </Button>
                        {deleteClick ? (
                          <DeleteModal
                            org="this organization"
                            handleDelete={handleDeleteOrganization}
                            deleteOrg
                          />
                        ) : (
                          <></>
                        )}
                      </div>
                    </Col>
                    <div className="avatar-wrapper pt-2 pb-4">
                      <Avatar>{organizationName.charAt(0)}</Avatar>
                      <span className="md-name">{organizationName}</span>
                    </div>
                    <Form className="input-md floating-label w-100" form={form}>
                      <Col span={24}>
                        <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                          <Col span={24} md={8} className="gutter-row">
                            <FloatLabel
                              label="Organization Name"
                              value={organizationDetails.name}
                            >
                              <Form.Item
                                className="required edit-input"
                                name="name"
                                id="name"
                                validateStatus="success"
                                rules={[
                                  {
                                    required: true,
                                    message: "Please enter organization name ",
                                  },
                                ]}
                              >
                                <Input
                                  className="input-control"
                                  id="name"
                                  name="name"
                                  className="required"
                                  defaultValue={organizationDetails.name}
                                  value={organizationDetails.name}
                                  htmlFor="name"
                                  autoComplete="off"
                                  disabled
                                />
                              </Form.Item>
                            </FloatLabel>
                          </Col>
                          <Col span={24} md={8} className="gutter-row">
                            <FloatLabel
                              label="Organization Id"
                              value={organizationDetails.identifier}
                            >
                              <Form.Item
                                className="required edit-input"
                                name="identifier"
                                id="identifier"
                                validateStatus="success"
                                rules={[
                                  {
                                    required: true,
                                    message: "Please enter organization id ",
                                  },
                                ]}
                              >
                                <Input
                                  className="input-control edit-input"
                                  name="identifier"
                                  id="identifier"
                                  htmlFor="identifier"
                                  className="required"
                                  defaultValue={organizationDetails.identifier}
                                  value={organizationDetails.identifier}
                                  autoComplete="off"
                                  disabled
                                />
                              </Form.Item>
                            </FloatLabel>
                          </Col>
                          <Col span={24} md={8} className="gutter-row">
                            <FloatLabel
                              label="Status"
                              value={organizationDetails.status}
                            >
                              <Form.Item
                                className="required edit-input"
                                name="status"
                                rules={[
                                  {
                                    required: true,
                                    message: "Please enter status ",
                                  },
                                ]}
                              >
                                <>
                                  <Select
                                    className="input-control w-100 w-lg-50"
                                    value={organizationDetails.status}
                                    htmlFor="status"
                                    onSelect={(value) =>
                                      handleOrganizationOnSelect(
                                        value,
                                        "status"
                                      )
                                    }
                                  >
                                    <Option value="1">Active</Option>
                                    <Option value="0">Inactive</Option>
                                  </Select>
                                </>
                              </Form.Item>
                            </FloatLabel>
                          </Col>
                          <Col span={24} md={8} className="gutter-row">
                            <FloatLabel label="Date" value="date1">
                              <Form.Item className="input-control datepicker-wrapper required">
                                <RangePicker
                                  className="w-100 "
                                  name="date1"
                                  value={organizationDetails.date1}
                                  onChange={(date) =>
                                    handleOrganizationDatePickerChange(
                                      date,
                                      "date1"
                                    )
                                  }
                                  disabledDate={disabledDate}
                                />
                              </Form.Item>
                            </FloatLabel>
                          </Col>
                        </Row>
                      </Col>
                      <Form.Item>
                        <div className="steps-action mt-5">
                          <Button
                            type="primary"
                            htmlType="submit"
                            onClick={onOrgEditSubmit}
                          >
                            Update
                          </Button>
                          <Button
                            className="ml-4"
                            type="secondary"
                            onClick={cancelEditing}
                          >
                            Cancel
                          </Button>
                        </div>
                      </Form.Item>
                    </Form>
                  </Col>
                </Row>
              </div>

              <div className="mid-content mt-4">
                <Row>
                  <Col
                    span={24}
                    className="d-flex justify-content-between align-items-center"
                  >
                    <h6 className="py-3 mb-0">Edit Seeded Users </h6>
                  </Col>
                  {seededUsers.length > 0 ? (
                    <AssignedUsers
                      seededUsers={seededUsers}
                      handleEditUser={handleEditSeededUser}
                      handleDelete={handleDeleteSeededUser}
                      viaEdit
                      seededUser
                    />
                  ) : (
                    <Col span={24} lg={18}>
                      <Row className="justify-content-center">
                        No Seeded User
                      </Row>
                    </Col>
                  )}
                  {editVisible ? (
                    <Modal
                      visible={editVisible}
                      onOk={handleEditOk}
                      onCancel={handleEditCancel}
                      centered
                      width={900}
                      okButtonProps={{
                        children: "Custom OK",
                      }}
                      cancelButtonProps={{
                        children: "Custom cancel",
                      }}
                      okText="Update"
                      cancelText="Cancel"
                    >
                      <Form
                        className="input-md floating-label w-100"
                        name="validate_other"
                      >
                        <div className="avatar-wrapper pt-2 pb-4">
                          <Avatar>{seededUserName.charAt(0)}</Avatar>
                          <span className="md-name">{seededUserName}</span>
                        </div>
                        {error && (
                          <Form.Item>
                            <Alert
                              message="Please fill the fields marked with asterisk(*)"
                              type="error"
                              showIcon
                            ></Alert>
                          </Form.Item>
                        )}
                        <Col span={24}>
                          <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                            <Col span={24} md={8} className="gutter-row">
                              <FloatLabel
                                label="First Name"
                                value={seededUserDetails.firstName}
                              >
                                <Form.Item
                                  name="firstName"
                                  className="required"
                                >
                                  <Input
                                    id="firstName"
                                    className="input-control"
                                    defaultValue={seededUserDetails.firstName}
                                    htmlFor="firstName"
                                    value={seededUserDetails.firstName}
                                    onChange={(e) =>
                                      handleSeededUserOnChange(e)
                                    }
                                    autoComplete="off"
                                  />
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                            <Col span={24} md={8} className="gutter-row">
                              <FloatLabel
                                label="Last Name"
                                value={seededUserDetails.lastName}
                              >
                                <Form.Item name="lastName" className="required">
                                  <Input
                                    className="input-control"
                                    id="lastName"
                                    htmlFor="lastName"
                                    className="required"
                                    defaultValue={seededUserDetails.lastName}
                                    value={seededUserDetails.lastName}
                                    onChange={(e) =>
                                      handleSeededUserOnChange(e)
                                    }
                                    autoComplete="off"
                                  />
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                            <Col span={24} md={8} className="gutter-row">
                              <FloatLabel
                                label="Email Id"
                                value={seededUserDetails.email}
                              >
                                <Form.Item name="email" className="required">
                                  <Input
                                    className="input-control"
                                    id="email"
                                    htmlFor="email"
                                    className="required"
                                    defaultValue={seededUserDetails.email}
                                    value={seededUserDetails.email}
                                    onChange={(e) =>
                                      handleSeededUserOnChange(e)
                                    }
                                    onBlur={(e) => validateEmailAddress(e)}
                                    autoComplete="off"
                                  />
                                  {emailValidationError && (
                                    <Alert
                                      message="Please enter a valid email address."
                                      type="error"
                                      showIcon
                                    ></Alert>
                                  )}
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                            <Col span={24} md={8} className="gutter-row">
                              <FloatLabel label="Date" value="date2">
                                <Form.Item className="input-control datepicker-wrapper required">
                                  <RangePicker
                                    className="new w-100"
                                    name="date2"
                                    className="required"
                                    value={seededUserDetails.date2}
                                    onChange={(date) =>
                                      handleSeededUserDatePickerChange(
                                        date,
                                        "date2"
                                      )
                                    }
                                    disabledDate={disabledDate}
                                  />
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                            <Col span={24} md={8} className="gutter-row">
                              <FloatLabel
                                label="Mobile"
                                value={seededUserDetails.mobile}
                              >
                                <Form.Item name="mobile" className="required">
                                  <Input
                                    className="input-control"
                                    id="mobile"
                                    htmlFor="mobile"
                                    className="required"
                                    defaultValue={seededUserDetails.mobile}
                                    value={seededUserDetails.mobile}
                                    onChange={(e) =>
                                      handleSeededUserOnChange(e)
                                    }
                                    onBlur={(e) => validateMobile(e)}
                                    autoComplete="off"
                                  />
                                  {mobileValidationError && (
                                    <Alert
                                      message="Please enter a valid mobile number."
                                      type="error"
                                      showIcon
                                    ></Alert>
                                  )}
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                            <Col span={24} md={8} className="gutter-row">
                              <FloatLabel
                                label="Status"
                                value={seededUserDetails.status2}
                              >
                                <Form.Item className="required" name="status2">
                                  <>
                                    <Select
                                      className="input-control w-100 w-lg-50"
                                      value={seededUserDetails.status2}
                                      htmlFor="status2"
                                      className="required"
                                      onSelect={(value) =>
                                        handleSeededUserOnSelect(
                                          value,
                                          "status2"
                                        )
                                      }
                                    >
                                      <Option value="1">Active</Option>
                                      <Option value="0">Inactive</Option>
                                    </Select>
                                  </>
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                          </Row>
                        </Col>
                      </Form>
                      <ResetPwdModal
                        user={seededUserDetails.firstName}
                        handleResetPasswordOk={handleResetPasswordOk}
                        handleResetPasswordCancel={handleResetPasswordCancel}
                      />
                    </Modal>
                  ) : (
                    <></>
                  )}
                </Row>
              </div>
            </Content>
          </Layout>
        </Layout>
      </Row>
    </>
  );
};

export default EditOrg;
